import React from "react";
import { Card, Select, Button, notification, Icon, Input, Row, Col, Form} from "antd";
import EmailEditor from 'react-email-editor';
import firebase from "firebase";
import UploadImg from "../components/UploadImg";

const { TextArea } = Input;
const formItemLayout = {
    labelCol: { span: 4 },
    wrapperCol: { span: 16 },
};

export default class EditorPage extends React.Component {
    state = {
        loading: false,
        iconLoading: false,
        postName: '',
        postTags: '',
        description: '',
        link: '',
        
    };

    componentDidMount(){
        this.postId = this.props.history.location.search.slice(4);
        this.userId = firebase.auth().currentUser.uid;
        // this.setState({
        //     postId : this.postId,
        //     // postName : this.postName,
        // })
        console.log('Edit id',this.postId);
        firebase.database().ref('posts').once('value',snapshot=>{
            const data = [];
            snapshot.forEach((childSnapshot) =>{
                if(childSnapshot.key  === this.postId){
                    const data = childSnapshot.val();
                    console.log('Edit post',data.content);
                    this.editor.loadDesign(JSON.parse(data.content));
                    this.setState({postName: data.title,
                        postTags: data.tags,
                        description: data.description,
                        link: data.link});
                    // this.setState({postTags: data.tags});
                    // this.setState({description: data.description});
                    // this.setState({link: data.link});
                    if(this.uploadImgView){
                        this.uploadImgView.updateLink(data.link);
                    }
                }
              data.push({
                ...childSnapshot.val(),
                key: childSnapshot.key});
              // ...
            });
          });
    }

    enterLoading = () => {
        this.setState({ loading: true });
    };

    save = () => {
        if(this.state.postName === '' || this.state.postTags === '' || this.state.description === ''){
            notification.open({
                message: 'Vui lòng nhập đầy đủ thông tin!',        
                icon: <Icon type="warning" style={{ color: '#FFC107' }} />,
              });
              return;
        }
        
        if(this.editor){
            this.editor.exportHtml(data=>{
                const { design, html } = data
                console.log('exportHtml', html)
                console.log('Save data',data);

                if(this.postId){
                    const email = firebase.auth().currentUser.email;
                    const urlImg = firebase.storage().ref();
                    
                    firebase.database().ref('posts/' + this.postId ).set({
                        content: JSON.stringify(design), 
                        title: this.state.postName, 
                        tags: this.state.postTags,
                        date: new Date().getTime(),   
                        author: email,
                        contentAsHTML: html,
                        description: this.state.description,
                        link: this.state.link,
                        // urlImg: urlImg,
                    })
                    
                } else{
                    console.log('user id',this.userId);
                    const email = firebase.auth().currentUser.email;
                    const urlImg = firebase.storage().ref();
                    
                    firebase.database().ref('posts').push({
                        date: new Date().getTime(),                       
                        title: this.state.postName,                        
                        author: email,
                        tags: this.state.postTags,
                        content: JSON.stringify(design),
                        contentAsHTML: html,
                        description: this.state.description,
                        link: this.state.link,
                    });
                    
                }
                notification.open({
                    message: 'Bài viết đã được lưu lại',        
                    icon: <Icon type="smile" style={{ color: '#108ee9' }} />,
                });
            });

        }
        
    };

    onChange(value) {
        console.log(`selected ${value}`);
    }
    onBlur() {
        console.log("blur");
    }
    onFocus() {
        console.log("focus");
    }
    onSearch(val) {
        console.log("search:", val);
    }
    onChangeName = event => {
        this.setState({postName:event.target.value});
    };
    onChangeTags = event => {
        this.setState({postTags: event.target.value});
    }
    onChangeDes = event => {
        this.setState({description: event.target.value});
    }
    onUploadSuccess = link => {
        console.log('ddaay la link: ' ,link);
        this.setState({link});
    }
   
    render() {
        return (          
            <>
                <Card
                    title={this.state.postId? "Sửa Tin Tức" : "Thêm Tin Tức"} 
                    bordered={false}
                    extra={
                        <div>
                            
                            <Button
                                type="primary"
                                className="bg-dark-blue "
                                loading={this.state.loading}
                                onClick={this.save}
                                
                            >
                                Lưu Lại
                            </Button>
                            
                        </div>
                    }
                >
                    <Row gutter={[{ xs: 8, sm: 16, md: 24, lg: 32 }, 20]}>
                        <Col className="gutter-row" span={12}>
                            <Form.Item {...formItemLayout} label="Tên Bài Viết: ">
                                <TextArea placeholder="Nhập tên bài viết"
                                value={this.state.postName}
                                onChange={this.onChangeName}   
                                                    
                                autoSize />
                            </Form.Item>
                            
                            <Form.Item {...formItemLayout} label="Tên Chủ Đề: ">
                                <TextArea placeholder="Nhập tên chủ đề"
                                value={this.state.postTags}
                                onChange={this.onChangeTags}    
                                                   
                                autoSize />
                            </Form.Item>
                            
                            <Form.Item {...formItemLayout} label="Mô Tả: ">
                            <TextArea
                                placeholder="Nhập mô tả bài viết"
                                autoSize={{ minRows: 2, maxRows: 6 }}
                                value={this.state.description}
                                onChange={this.onChangeDes}
                                />
                            </Form.Item>
                        </Col>
                        <Col className="gutter-row" span={12}>
                            <UploadImg onUploadSuccess={this.onUploadSuccess}
                            ref={ref => this.uploadImgView= ref} />
                            
                        </Col>
                        <Col className="gutter-row" span={24}>
                            <hr></hr>
                            <EmailEditor
                            style={{width: '2000px'}}
                            ref={ref=>this.editor = ref} />
                        </Col>
                    </Row>
                    
                </Card>
            </>
        );
    }

}
