import React from "react";
import firebase from "firebase";
var ReactDOMServer = require('react-dom/server');
var HtmlToReactParser = require('html-to-react').Parser;

export default class ViewPosts extends React.Component {
    state = {
        data: {}
    };

    componentDidMount(){
        this.postId = this.props.history.location.search.slice(4);
        this.userId = firebase.auth().currentUser.uid;
        this.setState({
            postId : this.postId,
            // postName : this.postName,
        })
        console.log('Edit id',this.postId);
        firebase.database().ref('posts').once('value',snapshot=>{
            const data = [];
            snapshot.forEach((childSnapshot) =>{
                if(childSnapshot.key  === this.postId){
                    const data = childSnapshot.val();
                    console.log('Edit post',data.content);
                    this.setState({data: data});
                    
                }
              // ...
            });
          });
    }

    render() {
        var htmlToReactParser = new HtmlToReactParser();
        var reactElement = htmlToReactParser.parse(this.state.data.contentAsHTML);
        var reactHtml = ReactDOMServer.renderToStaticMarkup(reactElement);
        return (
            
            <>
                {reactElement}
            </>
        );
    }
}
