import React from "react";
import { Col, Card, Typography } from "antd";
import firebase from 'firebase';
import * as utils from '../utils/utils'
import { Link } from "react-router-dom";

const { Meta } = Card;
const { Text } = Typography;
export default class CardPost extends React.Component {
  state = {
    dataSource: [
    ]
  };

  constructor(props) {
    super(props);
    firebase.database().ref('posts').once('value', snapshot => {
      const data = [];
      snapshot.forEach(function (childSnapshot) {
        data.push({
          ...childSnapshot.val(),
          key: childSnapshot.key
        });
        // ...
      });
      this.setState({ dataSource: data });
    });
  }


  render() {

    return (
      <>
        {this.state.dataSource.map(item =>

          <Col className="gutter-row" span={6}>
            <Link to={'/viewPost?id=' + item.key}>
              <Card
                className="cardPost"
                extra={<span className="card-post__category">{item.tags}</span>}
                cover={
                  <img
                    alt="example"
                    src={item.link}
                  />
                }

              >
                
                  <Meta
                    style={{marginBottom: 8}}
                    title={item.title}
                    description={item.description}
                  />
                  <Text code>{utils.convertTimeToString(item.date, "Ngày đăng: DD/MM/YYYY")}</Text>
              </Card>
              </Link>
            
          </Col>
        )}
      </>
    );
  }
}
